@import "variables";
@import "mixins";

/*
* Controls
* ================ */

.form-control {
	position: relative;
	margin: 0;
	padding: 0;
	background: transparent;
	box-shadow: none;
	border: none;
}

@keyframes flyOut {
	0% {
		transform: translate(0, 0) scale(1) rotate(0);
	}
	50% {
		transform: translate(150%, -150%) scale(0) rotate(-20deg);
	}
	51% {
		transform: translate(-50%, 150%) scale(0) rotate(-20deg);
	}
	100% {
		transform: translate(0, 0) scale(1) rotate(0);
	}
}

