.divider-dashed {
	width: 100%;
	display: block;
	margin: 30px 0 25px;
	float: left;
	position: relative;

	&:before {
		content: '';
		position: absolute;
		top: 50%;
		left: 0;
		height: 0;
		width: 100%;
		opacity: 0.2;
		border-bottom: 1px dashed;
	}
}
